.header {
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  transition: 0.5s;
  width: 100%;
  position: fixed;
  z-index: 900;
}

.header__content {
  max-width: 960px;
  margin-left: auto;
  margin-right: auto;
}

.header__content::after {
  clear: both;
  content: "";
  display: table;
}

.header__logo {
  height: 2.125rem;
  width: 8.75rem;
  background: url("/build/img/2016/startupdev-logo.svg") no-repeat top center;
  display: block;
  float: left;
  margin-top: 20px;
}

.header-navigation {
  float: right;
  margin-top: 1.5625rem;
}

.header-navigation__item {
  display: inline-block;
  font-family: "Avenir LT Std",sans-serif;
  font-size: .875rem;
  padding: 0 1.25rem;
}

.header-navigation__item a {
  height: 100%;
  width: 100%;
  -webkit-transition: all 0.2s;
  -moz-transition: all 0.2s;
  transition: all 0.2s;
  color: #fff;
  display: block;
  text-align: center;
}

.header-navigation__item:last-child {
  float: right;
  margin-bottom: 0.5rem;
  margin-top: -0.5rem;
}

.header-navigation__item:hover a {
  color: #28ead7;
}

.header-navigation__item--btn {
  border-radius: 4px;
  border: 1px solid #fff;
  padding: 6px 17px;
}

.header-navigation__item--btn::after {
  background: url("/build/img/2016/arrow-btn.svg") no-repeat top center;
  content: '';
  display: block;
  height: 9px;
  position: static;
  width: 63px;
}

.btn-toggle:hover::after,
.btn-toggle:hover::before {
  background: #28ead7;
}

.btn-toggle {
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  position: relative;
  top: 50%;
  cursor: pointer;
  margin-top: 8px;
  position: absolute;
  right: 1.25rem;
  text-decoration: none;
}

.btn-toggle:hover {
  background: #28ead7;
}

.btn-toggle,
.btn-toggle:before,
.btn-toggle:after {
  -webkit-transition: 0.3s ease-in-out;
  -moz-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
  background-color: #fff;
  display: none;
  height: 3px;
  width: 34px;
}

.btn-toggle>span {
  border: 0 none;
  color: transparent;
  height: 40px;
  margin: 0;
  overflow: hidden;
